<template>
    <div class="main">
        <div class="user_top">
            <div class="user_data">
                <img :src="userHead" alt="" class="head">
                <div class="name">{{userName}}</div>
            </div>
        </div>
        <!-- <div class="setting" @click="goSetting"></div> -->
        <div class="focus_container">
            <a href="/view/product_collect.html">
                <div class="num">{{collectProduct}}</div>
                <div class="name">收藏商品</div>
            </a>
            <a href="/view/shop_collect.html">
                <div class="num">{{collectShop}}</div>
                <div class="name">收藏店铺</div>
            </a>
        </div>
        <OrderMenu></OrderMenu>
        <div class="user_bottom">
            <a href="/view/address.html">地址管理</a>
            <a href="/view/question.html">常见问题</a>
        </div>
        <Bottom></Bottom>
    </div>
</template>
<script>
import Bottom from '../home/bottom_nav_index.vue'
import OrderMenu from './order_menu.vue'
export default {
    components: { Bottom, OrderMenu },
    data: () => {
        return {
            userHead: "/img/head.png",
            userName: "星雨漂流",
            collectProduct: 28,
            collectShop: 5
        }
    },
    created: function(){
        document.getElementById("bg_video").play();
    }
}
</script>
<style lang="scss">
* {
    margin: 0;
    padding: 0;
}
.main {
    width: 100%;
    max-width: 640px;
    margin: 0 auto;
    overflow: auto;
    padding-bottom: 60px;
}

.user_top {
    width: 100%; //background-image: url("/img/user_bg.png");
    background-color: #939;
    background-size: 100% 100%;
    height: 56vw;
    max-height: 360px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.focus_container {
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    a {
        text-decoration: none;
    }
    div {
        text-align: center;
    }
    .num {
        font-size: 1.2rem;
        color: #939;
    }
    .name {
        font-size: 0.9rem;
        color: #999;
    }
}

.user_data {
    width: 100%;
    text-align: center; // padding-bottom: 10%;
    //  padding-top: 10%;
    .head {
        width: 80px;
        height: 80px;
        border-radius: 50%;
        border: 2px solid #fff;
    }
    .name {
        color: #fff;
        font-weight: bold;
    }
}

.setting {
    background-image: url("/img/setting.png");
    background-size: 100% 100%;
    width: 25px;
    height: 25px;
    position: absolute;
    top: 1.5%;
    margin-left: 2%;
}

.user_bottom {
    width: 100%;
    margin-top: 3%;
    a {
        display: block;
        width: 90%;
        margin: 0 auto;
        padding: 18px 0;
        background-image: url("/img/forward.png");
        background-position: right;
        background-size: 20px 20px;
        background-repeat: no-repeat;
        text-decoration: none;
        color: #939;
    }
}
</style>


